                    
"use client"
import React, { useEffect, useState } from 'react';
import { FaUser } from 'react-icons/fa';

interface Jury {
  idJury: number;
  nom: string;
}

interface JurySelectorProps {
  selectedJury: string;
  onSelectJury: (jury: string) => void;
}

const JurySelector = ({ selectedJury, onSelectJury }: JurySelectorProps) => {
  const [jurys, setJurys] = useState<Jury[]>([]);
  const [loading, setLoading] = useState<boolean>(true);
  const [error, setError] = useState<string | null>(null);

  // Fetch jury data from the API
  useEffect(() => {
    const fetchJurys = async () => {
      try {
        const response = await fetch('http://localhost:5000/api/jurys'); // Replace with your backend URL
        if (!response.ok) {
          throw new Error('Failed to fetch jury data');
        }
        const data = await response.json();
        setJurys(data); // Set the fetched jury data
      } catch (err) {
        console.error('Error fetching jury data:', err);
        setError('Failed to fetch jury data');
      } finally {
        setLoading(false); // Set loading to false after fetching
      }
    };

    fetchJurys();
  }, []);

  if (loading) {
    return <div>Loading...</div>; // Show loading state
  }

  if (error) {
    return <div>{error}</div>; // Show error message
  }

  return (
    <div className="flex items-center gap-3 bg-gray-50 p-4 rounded-lg shadow-sm">
      <FaUser className="text-gray-600" />
      <select
        value={selectedJury}
        onChange={(e) => onSelectJury(e.target.value)}
        className="p-2 border rounded w-full focus:ring-2 focus:ring-blue-500"
      >
        <option value="">SÃ©lectionner un jury</option>
        {jurys.map((jury) => (
          <option key={jury.idJury} value={jury.nom}>
            {jury.nom}
          </option>
        ))}
      </select>
    </div>
  );
};

export default JurySelector;