"use client";

import { useState, useEffect } from "react";
import JurySelector from "../components/PlanifierSoutenance/JurySelector";

interface Jury {
  idJury: number;
  nom: string;
}

const PlanifierSoutenance = () => {
  const [jurys, setJurys] = useState<Jury[]>([]);
  const [loading, setLoading] = useState<boolean>(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    const fetchJurys = async () => {
      try {
        const response = await fetch("http://localhost:5000/api/jurys"); // Fetch from Express backend
  
        if (!response.ok) {
          throw new Error("Failed to fetch jury list");
        }
  
        const jurysData = await response.json();
        setJurys(jurysData);
      } catch (error) {
        console.error("Error fetching jurys:", error);
        setError("Failed to fetch jury list. Please try again.");
      } finally {
        setLoading(false);
      }
    };
  
    fetchJurys();
  }, []);
  

  if (loading) {
    return <div className="p-8 text-center">Loading jurys...</div>;
  }

  if (error) {
    return <div className="p-8 text-center text-red-500">{error}</div>;
  }

  return (
    <div className="p-8 bg-gray-50 min-h-screen">
      <h1 className="text-4xl font-bold text-gray-800 mb-8 text-center">ðŸ“… Jury Selection</h1>
      <div className="max-w-4xl mx-auto bg-white p-6 rounded-lg shadow-xl mb-8">
        <h2 className="text-2xl font-semibold text-gray-700 mb-6">Select a Jury</h2>
        <JurySelector jurys={jurys} selectedJury="" onSelectJury={() => {}} />
      </div>
    </div>
  );
};

export default PlanifierSoutenance;
